.accordion__element {
  border-bottom: 1px solid rgba(28, 51, 116, 0.2);
  display: flex;
  width: 100%;
  flex-wrap: wrap;

  h3 {
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    margin: 0;

    @include vp-1199 {
      max-width: 79.65%;
    }

    @include vp-767 {
      font-size: 18px;
      line-height: 21px;
      max-width: 82.07%;
    }
  }

  br {
    display: none;

    @include vp-350 {
      display: block;
    }
  }

  summary {
    position: relative;
    padding: 33px 0 21px;
    list-style-type: none;
  }

  span {
    position: absolute;
    content: "";
    width: 42px;
    height: 42px;
    background-color: transparent;
    border: 1px solid $color-default-sapphire;
    top: 25px;
    right: 0;
    border-radius: 50%;

    @include vp-767 {
      width: 32px;
      height: 32px;
      top: 35px;
    }
  }

  span::after,
  span::before {
    position: absolute;
    content: "";
    width: 17px;
    height: 1px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: $color-default-sapphire;

    @include vp-767 {
      width: 12px;
    }
  }

  span::before {
    transform: rotate(90deg);
  }

  &[open] {
    span::before {
      display: none;
    }

    span {
      background-color: $color-default-sapphire;

      &::after {
        background-color: $color-default-white;
      }
    }
  }

  @include vp-1199 {
    summary {
      padding-bottom: 25px;
    }

    &--schedule summary {
      padding-bottom: 28px;
    }

    &--coolers summary {
      padding-bottom: 30px;
    }

    &--trainers {
      h3 {
        padding-right: 62px;
      }

      .accordion__button {
        margin-top: 5px;
      }

      summary {
        padding-top: 27px;
        padding-bottom: 54px;
      }

      span {
        top: 35px;
      }
    }
  }

  @include vp-767 {
    summary {
      padding: 20px 0 22px;
    }

    &--trainers {
      h3 {
        padding-right: 0;
      }
    }

    &--center {
      h3 {
        max-width: 83%;
      }
    }

    &--center span,
    &--child span {
      top: 25px;
    }
  }

  &:first-child {
    border-top: 1px solid rgba(28, 51, 116, 0.2);
  }

  p {
    padding-right: 200px;
    font-size: 18px;
    font-family: $ff-roboto-light;
    font-weight: 300;
    line-height: 26px;
    margin: -9px 0 23px;

    @include vp-1199 {
      padding-right: 156px;
      line-height: 25px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 21px;
      padding: 0;
      margin-bottom: 26px;
    }
  }

  &--limit {
    summary {
      padding: 44px 0 28px;
    }

    span {
      top: 36px;
    }
  }
}
